import { HISTORIES } from 'statics';

const NormalDot = () => (<svg viewBox="0 0 12 12" className="w-3 h-3 mr-6 overflow-visible text-gray-300"><circle cx="6" cy="6" r="6" fill="currentColor"></circle><path d="M 6 -6 V -30" fill="none" strokeWidth="2" stroke="currentColor" className="text-gray-200"></path><path d="M 6 18 V 500" fill="none" strokeWidth="2" stroke="currentColor" className="text-gray-200"></path></svg>)
const LastDot = ()=>(<svg viewBox="0 0 12 12" className="w-3 h-3 mr-6 overflow-visible text-grey-300"><circle cx="6" cy="6" r="6" fill="currentColor"></circle><circle cx="6" cy="6" r="11" fill="none" stroke="currentColor" strokeWidth="2"></circle><path d="M 6 18 V 500" fill="none" strokeWidth="2" stroke="currentColor" className="text-gray-200"></path></svg>)

const History = () => {
    return (
        <div className="container animate-fade-in-top border-opacity-0"> 
            <ul className="bg-gray-50 rounded-3xl p-2 xs:p-0 xl:p-6 overflow-y-auto ss h-full">
                {
                    HISTORIES.map((history, idx) => (
                        <li key={`${history.dateTime}_${idx}`}>
                            <article>
                                <span className="grid md:grid-cols-8 xl:grid-cols-9 items-start relative rounded-xl p-3 sm:p-5 xl:p-6 overflow-hidden hover:bg-white">
                                    <div className="md:col-start-3 md:col-span-6 xl:col-start-3 xl:col-span-7 mb-1 ml-9 md:ml-0">
                                        <h3 className="text-lg font-semibold text-gray-900">
                                            {history.title}
                                        </h3>
                                        {history.skillset?.map((skillset, idx)=>(
                                            <span key={idx} className="truncate mr-1 pl-1 pr-1 mx-auto bg-indigo-100 text-indigo-500 font-xs rounded-md">
                                                {skillset}
                                            </span>
                                        ))}
                                        {history.image && <img className="object-contain md:object-scale-down" src={history.image} alt='profile'/>}
                                        <p className="font-sm">
                                            {history.description}
                                        </p>
                                    </div>
                                    <time dateTime="2021-04-05T19:00:00.000Z" className="md:col-start-1 md:col-span-2 row-start-1 md:row-end-3 flex items-center font-medium mb-1 md:mb-0">
                                        {idx===0 ? <LastDot/> :< NormalDot/> }
                                        {history.dateTime}
                                    </time>

                                </span>
                            </article>
                        </li>
                    ))
                }
            </ul>
            </div>
    )
}

export default History